@import url(https://fonts.googleapis.com/css2?family=Merriweather&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.container {
  text-align: center;
}

h2,
h4 {
  font-family: "Merriweather", serif;
}

.cardBox {
  display: flex;
  justify-content: center;
  align-items: center;
}

.main-caption {
  text-transform: uppercase;
  font-size: 40px;
  font-weight: bold;
}

#sub-heading {
  text-transform: uppercase;
}

.list-group-mine .list-group-item {
  background-color: #cad5d8;
  border-top: 1px solid #0091b5;
  border-left-color: #fff;
  border-right-color: #fff;
}
#tRecover {
  font-weight: bold;
}

#tRecover-num {
  color: #0a2de6;
  font-weight: bold;
}

#tDeath {
  font-weight: bold;
}

#tDeath-num {
  color: #ff0000;
  font-weight: bold;
}
.europe-icon {
  color: #347c17;
  position: relative;
  font-size: 40px;
  margin-left: 5px;
}

.corona-icon {
  color: #ca1f0b;
  position: relative;
  font-size: 40px;
  -webkit-animation: sunRotation 5s infinite linear;
          animation: sunRotation 5s infinite linear;
}

@-webkit-keyframes sunRotation {
  to {
    transform: rotate(360deg);
  }
}

@keyframes sunRotation {
  to {
    transform: rotate(360deg);
  }
}

